import React, { FC, Fragment, useState } from 'react';
import styled from '@theme';
import { default as MdxBackdrop } from './MdxBackdrop';
import CloseIcon from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';

const Container = styled.span`
    .img {
        max-width: 700px;
        box-shadow: ${(props) => props.theme.shadows[4]} !important;
        border-radius: 10px;
        cursor: pointer;
        :hover {
            box-shadow: ${(props) => props.theme.shadows[10]} !important;
        }
    }
    .backdrop {
        z-index: ${(props) => props.theme.zIndex.modal + 1};
        color: ${(props) => props.theme.palette.common.black};
        cursor: pointer;
        &__img-container {
            width: 90%;
            max-width: 700px;
            display: block;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &__img {
            position: static !important;
            width: 100%;
            height: 100%;
        }

        &__close-icon {
            color: ${(props) => props.theme.palette.common.white};
            position: absolute;
            top: 10px;
            right: 12px;
        }
    }
`;

const MdxImg: FC = (props) => {
    const [open, setOpen] = useState<boolean>(false);
    const handleClick = () => {
        setOpen(!open);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Container>
            <img {...props} onClick={handleClick} className="img" />
            <MdxBackdrop open={open} onClick={handleClose} className="backdrop">
                <IconButton className="backdrop__close-icon">
                    <CloseIcon />
                </IconButton>
                <span className="backdrop__img-container">
                    <img {...props} onClick={handleClick} className="backdrop__img" />
                </span>
            </MdxBackdrop>
        </Container>
    );
};

export default MdxImg;
