import React, { FC, Fragment, useState } from 'react';
import Fab from '@material-ui/core/Fab';
import MuiDrawer from '@material-ui/core/Drawer';
import CodeIcon from '@material-ui/icons/Code';
import CloseIcon from '@material-ui/icons/Close';
import Zoom from '@material-ui/core/Zoom';
import styled, { useTheme, Theme } from '@theme';
import { default as Content } from './Content';
import { ITableOfContents } from './blogTableOfContent';

const BlockContainer = styled.nav`
    ${(props) => props.theme.breakpoints.down('sm')} {
        display: none;
    }
`;

const IconContainer = styled.nav`
    z-index: ${(props) => props.theme.zIndex.drawer + 1};
    position: fixed;
    bottom: ${(props) => props.theme.spacing(20)}px;
    right: ${(props) => props.theme.spacing(2)}px;
    ${(props) => props.theme.breakpoints.up('md')} {
        display: none;
    }
    .fab {
        background-color: ${(props) => props.theme.palette.common.white};
        color: ${(props) => props.theme.palette.primary.main};
    }
`;

const Drawer = styled(MuiDrawer)`
    .drawer-list {
        min-height: 100vh;
    }
`;

type IProps = {
    tableOfContents: ITableOfContents;
    className?: string;
};
const BlogTableOfContent: FC<IProps> = ({ tableOfContents, className }) => {
    const theme: Theme = useTheme();
    const [open, setOpen] = useState<boolean>(false);
    const handleClickTocIcon = () => {
        setOpen(!open);
    };
    const handleClosedDrawer = () => {
        setOpen(false);
    };
    return (
        <Fragment>
            <BlockContainer className={className}>
                <Content tableOfContents={tableOfContents} />
            </BlockContainer>
            <IconContainer>
                <Fab size="large" className="fab" onClick={handleClickTocIcon}>
                    {open ? <CloseIcon /> : <CodeIcon />}
                </Fab>
            </IconContainer>
            <Drawer anchor="top" open={open} onClose={handleClosedDrawer} style={{ zIndex: theme.zIndex.drawer }}>
                <div className="drawer-list">
                    <Content tableOfContents={tableOfContents} onClickTableContent={handleClosedDrawer} />
                </div>
            </Drawer>
        </Fragment>
    );
};

export default BlogTableOfContent;
